//点击"做什么"标签
$(".sidebar").on("click", "li", function() {
    $(".what_content").removeClass("current_tag");
    $(".sidebar").children("li").removeClass("current");
    $(this).addClass("current");
    $(".what_content").css("display", "none");

    $(this).css("animation", "clickfeedback 1s");
    setTimeout(() => {
        $(this).css("animation", "");
    }, 500)

    let data_num = $(this).data("num");
    let content = "div[data-num='" + data_num + "']";

    $(content).css("display", "block");

})

//聚焦searchbar2时
$(".searchbar2").on("focus", function() {
    $(".hotsearch").css("display", "flex");
    $(".search_result").css("display", "none");
    $(".split_line").css("display", "none");
    $(".nonesearch").css("display", "none");

    if($(this).val().length != 0) {
        $(".header2 img").css("display", "inline");
    }
})

//当搜索框中有字时
$(".searchbar2").on("input", function() {
    if($(this).val().length == 0) {
        $(".header2 img").css("display", "none");
    }
    else {
        $(".header2 img").css("display", "inline");
    }
})


//当点击删除按钮时
$(".header2 img").on("touchstart", function(e) {
    // $(".searchbar2").focus();
    $(".searchbar2").val("");
    $(this).css("display", "none");
    if ( e && e.preventDefault )
        e.preventDefault();
})

//不能直接$("className")来调用addEventListener
