//点击热搜词语
$(document).on("touchstart", ".hotsearch_content span", function() {
    let keyword = this.innerHTML;
    let url = url_searchword + keyword;
    //将搜索框设置为热搜词
    $(".searchbar2").val(keyword);
    //隐藏热搜
    $(".hotsearch").css("display", "none");
    //清空容器内容
    $(".search_result_content").empty();
    //搜索框失去焦点
    $(".searchbar2").blur();
    
    $.getJSON(url, function(res) {
        let communities = res.data;
        if(communities != null) {
            $(".search_result").css("display", "flex");
            $(".split_line").css("display", "flex");
            let length = communities.length;
            for(let i = 0; i < length; i++) {
                let newa = $("<a />", {href:"#page3", class:"links"});
                //展示社团标题
                let newdiv = $("<div />", {class: "search_result_item"});
                let newh2 = $("<h2 />", {text:communities[i].name});
                newdiv.append(newh2);
                //展示社团简介
                let newcontent = $("<p />", {text:communities[i].introduction})
                newdiv.append(newcontent);
                //插入社团详细内容
                let newdetail = $("<p />", {text:communities[i].detail_info[0], class:"details"});
                newdiv.append(newdetail);
                newa.append(newdiv);
        
                $(".search_result_content").append(newa);

                // console.log(organizations[i].detail_info[0].length);
            }
        }
        else {
            $(".split_line").css("display", "none");
            $(".nonesearch").css("display", "flex");
        }
    })

})

//搜索框回车
$(".searchbar2").on("keypress", function(e) {
    var keycode = e.keyCode;
	if(keycode == '13') {
        let keyWord = $(this).val();
        this.blur();
        let url = url_searchword + keyWord;
        //隐藏热搜栏
        $(".hotsearch").css("display", "none");
        //清空容器内容
        $(".search_result_content").empty();

        $.getJSON(url, function(res) {
            let communities = res.data;
            if(communities != null) {
                $(".split_line").css("display", "flex");
                $(".search_result").css("display", "flex");
                let length = communities.length;
                for(let i = 0; i < length; i++) {
                    let newa = $("<a />", {href:"#page3", class:"links"});
                    //展示社团标题
                    let newdiv = $("<div />", {class: "search_result_item"});
                    let newh2 = $("<h2 />", {text:communities[i].name});
                    newdiv.append(newh2);
                    //展示社团简介
                    let newcontent = $("<p />", {text:communities[i].introduction})
                    newdiv.append(newcontent);
                    //插入社团详细内容
                    let newdetail = $("<p />", {text:communities[i].detail_info[0], class:"details"});
                    newdiv.append(newdetail);
                    newa.append(newdiv);
            
                    $(".search_result_content").append(newa);
                }
            }
            else {
                $(".split_line").css("display", "none");
                $(".nonesearch").css("display", "flex");
            }
        })
	}
})

//点击详情页面
