//滑动一定页面出现阴影/标题
$("#page3").scroll(function() {
    let s = $(this).scrollTop();
    let current_h1 = this.childNodes[3].childNodes[0];
    // let title = current_h1.innerHTML;
    let height = current_h1.offsetHeight;
    if(s > height) {
        $(".header3").css("box-shadow", "rgba(0,0,0,.2) 0 0.0625rem 0.3125rem 0");
        $(".header3 span").show();
    }
    else {
        $(".header3").css("box-shadow", "none");
        $(".header3 span").hide();
    }
})



//滑动返回上一页手势
var startX, startY;
var endX, endY;
$("#page2").on("touchstart", function(e) {
    startX = e.touches[0].pageX;
    startY = e.touches[0].pageY;
})
$("#page3").on("touchmove", function(e) {
    endX = e.touches[0].pageX;
    endY = e.touches[0].pageY;

    let x = endX - startX;
    let y = endY - startY;

    if(Math.abs(x) > Math.abs(y) && x > 0) {
        preventDefault();
        history.back();
    }
})

$("#page3").on("touchstart", function(e) {
    startX = e.touches[0].pageX;
    startY = e.touches[0].pageY;
})
$("#page3").on("touchmove", function(e) {
    endX = e.touches[0].pageX;
    endY = e.touches[0].pageY;

    let x = endX - startX;
    let y = endY - startY;

    if(Math.abs(x) > Math.abs(y) && x > 0) {
        preventDefault();
        history.back();
    }
})

